html, 
body, 
#background { 
  background-color: #FCF;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  margin-top: 30px;
}

#root {
  height: 100%;
}

.Country-Card {
  display: inline;
}

.Country-Card Button {
  cursor: pointer;
  font-size: .5em;
  align-self: end;
}

.Country-Card Divider {
  width: 100%;
}

.Second-Row, .Third-Row, .Fourth-Row {
  font-size: 1.5em;
}
